import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    sideNav: false,
    subNavItems: [],
    navItems: [
      {
        name: 'About',
        path: 'about',
        menus: ['toolbar'],
        auth: ['all'],
        color: 'red',
        fontColor: "white",
      },
      {
        name: 'Home',
        path: '/',
        menus: ['toolbar','drawer'],
        auth: ['all'],
        color: 'blue',
        fontColor: "white",
      },
      {
        name: 'Chat',
        path: 'chat',
        menus: ['drawer','home'],
        auth: ['all'],
        color: 'yellow',
      },
      {
        name: 'Resource',
        path: 'documents',
        menus: ['toolbar','drawer','home'],
        auth: ['all'],
        color: 'green',
      },
      {
        name: 'Central',
        path: 'retreat/central',
        menus: ['drawer','home'],
        auth: ['all'],
        color: 'purple',
      },
      {
        name: 'East',
        path: 'retreat/east',
        menus: ['drawer','home'],
        auth: ['all'],
        color: 'purple',
      },
      {
        name: 'Southwest',
        path: 'retreat/southwest',
        menus: ['drawer','home'],
        auth: ['all'],
        color: 'purple',
      },
      {
        name: 'South',
        path: 'retreat/south',
        menus: ['drawer','home'],
        auth: ['all'],
        color: 'purple',
      },
      {
        name: 'Admin',
        path: 'adminnav',
        menus: ['toolbar','drawer','home'],
        auth: ['all'],
        color: 'purple',
      },
      {
        name: 'item1',
        path: 'item1',
        menus: ['adminnav'],
        auth: ['all'],
        color: 'black',
      },
      {
        name: 'item2',
        path: 'item2',
        menus: ['adminnav'],
        auth: ['all'],
        color: 'black',
      },
    ]
  },
  getters: {
    sideNav: state => state.sideNav,
    menuItems: state => state.menuItems,
    subNavItems: state => state.subNavItems,
    toolbarNavItems: function(state) {
      return state.navItems.filter(e => e.menus.includes('toolbar') || e.menus.includes('all'))
    },
    homeNavItems: function(state) {
      return state.navItems.filter(e => e.menus.includes('home') || e.menus.includes('all'))
    },
    drawerNavItems: function(state) {
      return state.navItems.filter(e => e.menus.includes('drawer') || e.menus.includes('all'))
    },
    adminNavItems: function(state) {
      return state.navItems.filter(e => e.menus.includes('adminnav'))
    },
  },
  mutations: {
    sideNav (state, payload) {
      state.sideNav = payload
    },
    subNavItems (state, payload) {
      state.subNavItems = payload
    }
  },
  actions: {

  }
})
